/**
 * @file Renders the header of the page, including branding, title and searchbar
 * @author Adrian Rossino
 */

import { ChangeEvent, Dispatch, SetStateAction } from 'react';
import './style.css';
import logo from './images/photo-album.png';
import "bootstrap-icons/font/bootstrap-icons.css";

interface HeaderProps {
  setKeyword: Dispatch<SetStateAction<string>>;
  search: string;
  setSearch: Dispatch<SetStateAction<string>>;
}

const Header = ({setKeyword, setSearch, search}: HeaderProps) => {

  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    let keywords = e.target.value
    setSearch(keywords);
    /* 
      The story asks for a single word, so if the user inserts more than
      one word we trim it down to the first inserted word
     */
    const word = keywords.split(' ')[0];
    setKeyword(word);
  };


  return (
    <header id="header">
      <nav className="navbar navbar-expand-md fixed-top bg-dark navbar-dark">
        <div className="container">
          <span className="navbar-brand">
            <img src={logo} alt="Album app" className="d-inline-block align-text-top"/>
            Photo album app
          </span>
          <button 
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#searchbar">
            <i className="bi bi-search"></i>
          </button>
          <div id="searchbar" 
            className="collapse navbar-collapse">
            <div className="navbar-nav ms-auto col-10">
              <form 
                className="d-flex col-12"
                onSubmit={(e) => e.preventDefault()}>
                <input 
                  className="form-control me-2 input-large" 
                  type="search" 
                  placeholder="Search" 
                  aria-label="Search"
                  onChange={onChange}
                  data-testid={'searchBar'}
                  value={search}/>
              </form>
            </div>
          </div>
        </div>
      </nav>
    </header>
  )
}

export default Header
