/**
 * @file Photo component, renders an image with a title
 * @author Adrian Rossino
 */

import { Photo } from "./models/Photo"
import './style.css';

interface PhotoProps {
  photo: Photo;
  keyword: string;
}

export const PhotoComponent = ({ photo, keyword }: PhotoProps) => {

  let photoTitleArray = photo.title.split(' '); //Divide the title in words

  return (

    <div id={`photo_${photo.id}`}
      data-testid={`photo_${photo.id}`}
      className="col col-md-3 col-sm-6 col-lg-3 photo-component col-6">
      <img
        src={photo.thumbnailUrl}
        alt="album memory"
        className="album-photo rounded mx-auto d-block" />
      <span className="photo-title" data-testid={`photo_title_${photo.id}`}>
        {photoTitleArray.map((word, index) =>
          keyword.length > 0 && word.toLowerCase().includes(keyword.toLowerCase()) ?
            <b key={index}>{`${word} `}</b> : `${word} `
        )}
      </span>
    </div>
  )
}
