/**
 * @file Photo model
 * @author Adrian Rossino
 */

export class Photo {

  private albumIdValue: number;
  private idValue: number;
  private titleValue: string;
  private urlValue: string;
  private thumbnailUrlValue: string;

  constructor (obj: {albumId: number; id: number; title?: string; url?: string; thumbnailUrl?: string}) {
    this.albumIdValue = obj.albumId;
    this.idValue = obj.id;
    this.titleValue = obj.title || '';
    this.urlValue = obj.url || '';
    this.thumbnailUrlValue = obj.thumbnailUrl || ''; 
  }

  set setTitle (title: string) {
    this.titleValue = title;
  }

  set setUrl (url: string) {
    this.urlValue = url;
  }

  set thumbnailUrl (thumbnailUrl: string) {
    this.thumbnailUrlValue = thumbnailUrl;
  }

  get albumId (): number {
    return this.albumIdValue;
  }

  get id (): number {
    return this.idValue;
  }

  get title (): string {
    return this.titleValue;
  }

  get url (): string {
    return this.urlValue;
  }

  get thumbnailUrl (): string {
    return this.thumbnailUrlValue;
  }

}