/**
 * @file Album component, renders an image with a title
 * @author Adrian Rossino
 */

import { Album } from './models/Album';
import { Link } from 'react-router-dom';
import cover from './images/albumGeneric.jpg';
import './style.css';

interface AlbumProps {
  onClick(id: number): void;
  album: Album;
  keyword: string;
}

const AlbumComponent = ({ album, onClick, keyword }: AlbumProps) => {

  /**
   * Looks for album cover image or sets default cover image if none is found
   */
  let coverPicture = cover;
  if (album.coverImage) {
    coverPicture = album.coverImage;
  }

  let albumTitleArray = album.title.split(' '); //Divide the title in words

  return (

    <div id={`album_${album.id}`}
      data-testid={`album_${album.id}`}
      className="col col-md-3 col-sm-6 col-lg-2 album-component col-6"
      onClick={() => onClick(album.id)}>
      <Link
        to={`/${album.id}`}
        className="album-link">
        <img
          src={coverPicture}
          alt="album"
          className="album-cover rounded mx-auto d-block" />
        <span className="album-title"
          data-testid={`album_title_${album.id}`}>
          {albumTitleArray.map((word, index) =>
            keyword.length > 0 && word.toLowerCase().includes(keyword.toLowerCase()) ?
              <b key={index}>{`${word} `}</b> : `${word} `
          )}
        </span>
      </Link>
    </div>
  )
}

export default AlbumComponent;
