/**
 * @file Renders all Photos sent
 * @author Adrian Rossino
 */

import { Photo } from "./models/Photo";
import { PhotoComponent } from "./PhotoComponent";
import { Link, useParams } from 'react-router-dom';
import "bootstrap-icons/font/bootstrap-icons.css";
import { useEffect } from "react";

interface PhotosProps {
  albumName: string;
  keyword: string;
  clearKeyword(): void;
  photos: Photo[];
  setSelectedAlbum: Function;
}


const Photos = ({ albumName, keyword, clearKeyword, photos, setSelectedAlbum }: PhotosProps) => {

  /**
   * Parameter selected album, retrieves album id
   */
  let { selectedAlbum } = useParams<{ selectedAlbum: string }>();

  /**
   * Lyfecycle method, updates selectedAlbum state when selectedAlbum changes
   */
  useEffect(() => {
    setSelectedAlbum(selectedAlbum);
  }, [selectedAlbum, setSelectedAlbum]);

  let filteredPhotos = photos;
  if (keyword.length > 0) {
    filteredPhotos = photos.filter(photo => photo.title.toLowerCase().includes(keyword.toLowerCase()));
  }

  return (
    <div id="photos" className="container">
      <div className="row">
        <div className="col-3 col-sm-2 col-md-1">
          <Link to='/' >
            <i className="bi bi-arrow-left-circle back-button"
              data-testid={'backButton'}
              onClick={clearKeyword}>
            </i>
          </Link>
        </div>
        <div className="col-9 col-sm-10 col-md-11">
          <h1 className="display-5 text-start">
            {albumName}
          </h1>
        </div>
      </div>
      <div className="row">
        {
          filteredPhotos.length > 0 ?
            filteredPhotos.map(photo => (
              <PhotoComponent
                photo={photo}
                key={photo.id}
                keyword={keyword} />
            )) :
            (
              <div
                className="alert alert-warning"
                role="alert">
                No pictures match your criteria
              </div>
            )
        }
      </div>
    </div>
  )
}

export default Photos