/**
 * @file Service, handles calls to photos and albums apis
 * @author Adrian Rossino
 */

import { Album } from "../components/Album/models/Album";
import { Photo } from "../components/Photo/models/Photo";

class AlbumServie {

  private albumUrl: string = 'https://jsonplaceholder.typicode.com/users/{userId}/albums'; // Album rest api url
  private photosUrl: string = 'https://jsonplaceholder.typicode.com/albums/{albumId}/photos'; // Photo rest api url

  /**
   * Recovers all albums that belong to a user
   * @param {number} userId 
   * @returns 
   */
  public getAlbums = async (userId: number): Promise<Album[]> => {
    const url = this.albumUrl.replace('{userId}', userId.toString());
    const responseJson = await this.makeRequest(url);
    const albums = responseJson.map((album:any) => new Album(album));
    for (const album of albums) {
      await this.getCoverPicture(album);
    }
    return albums;
  }

  /**
   * Recovers all the pictures of an album 
   * @param {string} id Album id to get pictures
   * @returns 
   */
  public getPictures = async (id: string): Promise<Photo[]> => {
    const url = this.photosUrl.replace('{albumId}', id);
    const responseJson = await this.makeRequest(url);
    const photos = responseJson.map((photo: any) => new Photo(photo));
    return photos;
  }

  /**
   * Recovers the first picture of an album and sets it as the cover photo
   * @param {Album} album Album to get first picture
   * @returns 
   */
  public getCoverPicture = async (album:Album): Promise<void> => {
    const query = '?_limit=1';
    const url = this.photosUrl.replace('{albumId}', album.id.toString()) + query;
    const responseJson = await this.makeRequest(url);
    if (responseJson.length > 0) {
      album.setCoverImage = responseJson[0].thumbnailUrl;
    }
  }

  /**
   * Generic GET request, could implement a generic request with POST, PUT, etc adding params
   * @param {string} url Url to make the request to
   * @return Json Object from the response
   */
  public makeRequest = async (url: string): Promise<any> => {
    try {
      let response = await fetch(url);
      this.validateResponse(response);
      let responseJson = await response.json();
      if (responseJson.length > 0) {
        return responseJson;
      } else {
        return [];
      }
    } catch (e) {
      console.log(e.message);
      return [];
    }
  }

  /**
   * Validates the response from the server to look for status errors
   * @param response 
   */
  public validateResponse = (response: Response) => {
    if (response.status !== 200) {
      throw new Error(`Service returned an error - status:${response.status}`);
    }
  }

}
export const albumService = new AlbumServie();