
/**
 * @file Album model
 * @author Adrian Rossino
 */

export class Album {

  private userIdValue: number;
  private idValue: number;
  private titleValue: string;
  private coverImageValue: string;

  constructor (obj: {userId: number; id: number; title?: string; coverImage?: string}) {
    this.userIdValue = obj.userId;
    this.idValue = obj.id;
    this.titleValue = obj.title || '';
    this.coverImageValue = obj.coverImage || '';
  }

  set setTitle (title: string) {
    this.titleValue = title;
  }

  set setCoverImage (coverImage: string) {
    this.coverImageValue = coverImage;
  }

  get userId (): number {
    return this.userIdValue;
  }

  get id (): number {
    return this.idValue;
  }

  get title (): string {
    return this.titleValue;
  }

  get coverImage (): string {
    return this.coverImageValue;
  }

}