/**
 * @file Renders all Albums sent
 * @author Adrian Rossino
 */

import { Album } from "./models/Album";
import AlbumComponent from './AlbumComponent';

interface AlbumsProps {
  onClick(id: number): void;
  albums: Album[];
  keyword: string;
}

const Albums = ({onClick, albums, keyword}:AlbumsProps) => {

  /**
   * Filter albums by keyword if keyword exists
   */
  let filteredAlbums = albums;
  if (keyword.length > 0) {
    filteredAlbums = albums.filter(album => album.title.toLowerCase().includes(keyword.toLowerCase()));
  }

  return (
    <div className="row">
      {
        filteredAlbums.length > 0 ?
          filteredAlbums.map(album => (
            <AlbumComponent
              album={album}
              onClick={onClick}
              key={album.id}
              keyword={keyword} />
          )) :
          (
            <div
              className="alert alert-warning"
              role="alert">
              No albums match your criteria
            </div>
          )
      }
    </div>
  )
}

export default Albums
