/**
 * @file Main component, handles routing and app states
 * @author Adrian Rossino
 */

import './App.css';
import Header from '../Header/Header';
import { useState, useEffect } from 'react';
import { albumService } from '../../services/AlbumService';
import { Album } from '../Album/models/Album';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Albums from '../Album/Albums';
import Photos from '../Photo/Photos';
import { Photo } from '../Photo/models/Photo';

const App = () => {

  /**
   * States 
   */
  const [albums, setAlbums] = useState<Album[]>([]);
  const [search, setSearch] = useState('');
  const [keyword, setKeyword] = useState<string>('');
  const [albumName, setAlbumName] = useState<string>('');
  const [photos, setPhotos] = useState<Photo[]>([]);
  const [selectedAlbum, setSelectedAlbum] = useState<string>('');


  /**
   * Lyfecycle method to load albums on page load
   */
  useEffect(() => {
    const userId = 1; //We could get the user id from session or someplace else
    const getAlbums = async () => {
      const albums = await albumService.getAlbums(userId);
      setAlbums(albums);
    }
    getAlbums();
  }, []);

  /**
   * Lyfecycle method to load and update album photos when selected album changes
   */
  useEffect(() => {
    const getPhotos = async () => {
      if (selectedAlbum.length > 0) {
        const photos = await albumService.getPictures(selectedAlbum);
        setPhotos(photos);
      }
    }
    getPhotos();

  }, [selectedAlbum]);

  /**
   * On album click event function to clear keyword search and set album name state
   * @param id Album id
   */
  const handleAlbumOnClick = async (id: number) => {
    clearKeyword();
    const album = albums.find(album => album.id === id);
    if (album) {
      setAlbumName(album?.title);
    }
  }

  /**
   * Clear keyword search
   */
  const clearKeyword = () => {
    setSearch('');
    setKeyword('');
  }

  return (
    <Router>
      <div className="App">
        <Header
          setKeyword={setKeyword}
          search={search}
          setSearch={setSearch} />
        <div className="container">
          <Route path="/" exact render={(props) => (
            <Albums
              onClick={handleAlbumOnClick}
              albums={albums}
              keyword={keyword} />
          )} />
          <Route path="/:selectedAlbum" exact render={(props) => (
            <Photos
              albumName={albumName}
              keyword={keyword}
              clearKeyword={clearKeyword}
              photos={photos}
              setSelectedAlbum={setSelectedAlbum} />
          )} />
        </div>
      </div>
    </Router>
  );
}

export default App;
